.jivocb-container {
  perspective: 1000;
  position: fixed;
  bottom: 45px; /*position Y*/
  left: 50px; /*position X*/
}
.jivocb-container:hover .jivocb-wrap,
.jivocb-container.hover .jivocb-wrap {
  transform: rotateY(180deg);
}

.jivocb-container,
.jivocb-front,
.jivocb-back {
  width: 80px;
  height: 80px;
  border-radius: 80px;
}

.jivocb-wrap {
  transition: 0.6s;
  transform-style: preserve-3d;

  position: relative;
}

.jivocb-front,
.jivocb-back {
  backface-visibility: hidden;

  position: absolute;
  top: 0;
  left: 0;
}

.jivocb-front {
  z-index: 2;
  transform: rotateY(0deg);
  background: url("call-button.png") no-repeat;
  background-size: 90%;
  background-position: 50% 50%;
  animation: color 6s linear infinite;
  box-shadow: 0 0 0px 0px white, 0 0 6px 10px #44bb6e; /*Green animation*/
}
@keyframes color {
  10%,
  15%,
  35%,
  40%,
  60%,
  65%,
  85%,
  90% {
    box-shadow: 0 0 0px 0px white, 0 0 0px 6px grey;
  }
}

.jivocb-back {
  transform: rotateY(180deg);
  box-shadow: 0 0 0px 4px white, 0 0 0px 6px grey;
}
.jivocb-container .jivocb-back {
  transform: rotateX(180deg);
}

.jivocb-container .jivocb-wrap {
  transform-origin: 100% 40px;
}

.jivocb-container:hover .jivocb-wrap {
  transform: rotateX(-180deg);
}
.jivocb-call {
  background: url("jcall.png") no-repeat #fff;
  background-size: 100%;
  background-position: 50% 50%;
  width: 80px;
  height: 40px;
  border-radius: 40px 40px 0 0;
  -moz-border-radius: 40px 40px 0 0;
  -webkit-border-radius: 40px 40px 0 0;
}

.jivocb-chat {
  background: url("jchat.png") no-repeat #fff;
  background-size: 100%;
  background-position: 50% 50%;
  width: 80px;
  height: 40px;
  border-radius: 0 0 40px 40px;
  -moz-border-radius: 0 0 40px 40px;
  -webkit-border-radius: 0 0 40px 40px;
}
.jivocb-call:hover {
  background: url("jcall.png") no-repeat #44bb6e;
  background-size: 100%;
  background-position: 50% 50%;
  cursor: pointer;
}
.jivocb-chat:hover {
  background: url("jchat.png") no-repeat #44bb6e;
  background-size: 100%;
  background-position: 50% 50%;
  cursor: pointer;
}

.jivo_wrapper {
  position: fixed;
  right: 25px;
  bottom: 50px;
  z-index: 8;
  .labelButtons_9683._bottom_1a26:after,
  .bottomCorner_b7b0:after {
    display: none;
  }
}

.jivo_labels {
  position: absolute;
  bottom: 60px;
  right: 0;
  display: none;
  opacity: 0;
  transition: opacity 0.2s;
  border: 1px solid $-color_9;
  .labelButton_n:first-child .lab_block {
    border-top: none;
  }
  .lab_block {
    width: 50px;
    height: 50px;
    display: block;
    background-color: $-color_w;
    border-top: 1px dashed #999999;
    background-position: center center;
    background-repeat: no-repeat;
    transition: 0.2s;
    cursor: pointer;

    &.vk {
      background: $-color_w url("../svg/jivo.svg#jivo_chat_vk-view") no-repeat center center/30px auto;
    }
    &.fb {
      background: $-color_w url("../svg/jivo.svg#jivo_chat_fb-view") no-repeat center center/30px auto;
    }
    &.wa {
      background: $-color_w url("../svg/jivo.svg#jivo_chat_wa-view") no-repeat center center/31px auto;
    }
    &.chat {
      background: $-color_w url("../svg/jivo.svg#jivo_chat_chat-view") no-repeat center center/28px auto;
    }
    &:hover {
      background-color: #ededed;
    }
  }
}
.jivo_wrapper:hover {
  .jivo_labels {
    display: block;
    opacity: 1;
  }
}
.jivo_open_button {
  width: 51px;
  height: 61px;
  display: block;
  cursor: pointer;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 42px;
    background: url("../svg/jivo.svg#jivo_chat-view") no-repeat center bottom/51px auto;
  }
}

.jivo_mobile {
  display: none;
}
.jivo_mob_button {
  display: none;
}

@media screen and (max-width: 992px) {
  .jivo_mob_button {
    display: block;
  }
  .jivo_wrapper {
    display: none !important;
  }
  .jivo_mob_button {
    position: fixed;
    bottom: 75px;
    right: 15px;
    z-index: 88;
  }
  .jivo_mobile {
    .text_1373 {
      cursor: pointer;
    }
  }
  .jivo_open_button:before {
    background: url("../svg/jivo.svg#jivo_chatmob-view") no-repeat center bottom/50px auto;
  }
}
